.singlepost {
  flex: 9;
}

.singlepostwrapper {
  padding: 20px;
  padding-right: 0;
}

.singlepostimg {
  width: 100%;
  height: 400px;
  border-radius: 5px;
  object-fit: cover;
}

.singleposttitle {
  text-align: center;
  margin: 10px;
  font-size: 28px;
  font-family: "Lora", serif;
}

.singlepostedit {
  float: right;
  font-size: 16px;
}

.singleposticon {
  margin-left: 10px;
  cursor: pointer;
}
.singleposticon:first-child {
  color: teal;
}
.singleposticon:last-child {
  color: rgb(255, 51, 0);
}

.singlepostinfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #b39656;
  font-family: "Varela Round", sans-serif;
}

.singlepostdesc {
  color: #666;
  font-size: 18px;
  line-height: 25px;
}

.singlepostdesc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}
