.post {
  width: 485px;
  margin: 25px 25px 40px 25px;
}

.postimg {
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postinfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postcategory {
  font-family: "Varela Round", sans-serif;
  font-size: 13px;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.posttitle {
  font-family: "Josefin Sans", sans-serif;
  font-size: 24px;
  font-weight: 700;
  margin-top: 15px;
  cursor: pointer;
}

.postdate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  color: #999;
  margin-top: 15px;
}

.postdesc {
  font-family: "Varela Round", sans-serif;
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
}
